t: 72px;\n}\n.text_huge {\n    font-family: sans-serif-medium;\n    font-weight: 500;\n    font-size: 54px;\n    line-height: 78px;\n}\n.more {\n    position: absolute;\n    right: 18px;\n    bottom: 0;\n    font-family: sans-serif-medium;\n    font-weight: 500;\n}\n.test {\n    flex-direction: row;\n}\n</style>\n\n<script>\nimport Locale from 'weex-flymeui/lib/mixins/locale'\nimport { t } from 'weex-flymeui/lib/locale'\n\nexport default {\n    name: 'FmFoldableText',\n    mixins: [Locale],\n    data() {\n        return {\n            expandHeight: '',\n            unexpandHeight: '',\n            animationHeight: '',\n            foldText: '',\n            foldable: true\n        }\n    },\n    props: {\n        width: {\n            type: Number,\n            default: 1020\n        },\n        text: {\n            type: String,\n            default: ''\n        },\n        lines: {\n            type: Number,\n            default: 2\n        },\n        folded: {\n            type: Boolean,\n            default: true\n        },\n        small: {\n            type: Boolean,\n            default: true\n        },\n        large: Boolean,\n        huge: Boolean,\n        textStyle: Object,\n        tipStyle: Object,\n        tipValue: {\n            type: String,\n            default() {\n                return t('el.foldabletext.more')\n            }\n        }\n    },\n    mounted() {\n        if (this.foldText === '') {\n            const { textStyle } = this\n            let fontSize = (textStyle && textStyle.fontSize) ? textStyle.fontSize : this.large ? 48 : this.huge ? 54 : 36\n            // 计算折叠后的文本\n            let size1 = fontSize + 36 * 0.04 // 汉字\n            let size2 = fontSize * 0.56 // 英文\n            let size3 = fontSize * 0.556 // 数字\n            let size4 = fontSize * 0.80 // 全角\n            let size5 = fontSize * 0.2 // 半角\n            let tSize = 0, tmpStr = ''\n            let hideWidth = this.width * this.lines + fontSize // 不显示“更多”情况下所能容纳最大的字体