z.push('text_large')\n            } else if (this.huge) {\n                clz.push('text_huge')\n            }\n            return clz\n        }\n    },\n    methods: {\n        fold() {\n            this.foldable && (this.folded = !this.folded)\n        }\n    }\n}\n</script>\n"],"sourceRoot":""}]);

